export default {
	data() {
		return {
			system:this.systemInfo(),
			user:this.getUserInfo(),
			listType:1,
			goodslist:[]
		}
	},
	onLoad(option) {
		console.log(option);
		
		
		this.postJson({
			path:"/seller_order_list.jsp",
			data:{
				uid:this.user.uid,
				listType:this.listType
			},
			success:(res) => {
				if("ok" == res.data.status){
					this.goodslist = res.data.data;
					for(let x in this.goodslist){
						this.goodslist[x].stateLabel = this.getStateLabel(this.goodslist[x].status);
					}
				}
			}
		});
		
	},
	methods: {
		tabSelect:function(e){
			
			uni.showLoading({
				title:"加载中",
				mask:true
			});
			let index = e.currentTarget.dataset.id;
			this.postJson({
				path:"/seller_order_list.jsp",
				data:{
					uid:this.user.uid,
					listType:index
				},
				success:(res) => {
					uni.hideLoading();
					if("ok" == res.data.status){
						this.goodslist = res.data.data;
						this.listType = index;
					}
				}
			});
		},
		contactBuyer:function(e){
			let index = e.currentTarget.dataset.index;
			let order = this.goodslist[index];
			uni.makePhoneCall({
				phoneNumber:this.order.phone
			});
		},
		getStateLabel:function(state){
			console.log("getStateLabel,state="+state+",type="+typeof(state));
			if(this.listType == 4){
				switch (state) {
					case 0:
					{
						return "退货申请";
					}
						break;
					case 1:
					{
						return "已退货";
					}
						break;
					case 2:
					{
						return "拒绝退货";
					}
						break;
						
					default:
						break;
				}
			}else{
				switch (state) {
					case 0:
					{
						return "待付款";
					}
						break;
					case 1:
					{
						return "待发货";
					}
						break;
					case 2:
					{
					   return "待确认";
					}
						break;
					case 3:
					{
						
						return "已完成";
					}
						break;
					case 4:
					{
						return "已退货";
					}
						break;
					case 5:
					{
						return "已取消";
					}
						break;
					default:
						break;
				}
			}
			
			return "";
		}
	}
}
